// PACKAGES
import Vue from 'vue'
// import WebpPlugun from 'vue-webp-plugin';
import VueWebP from 'v-webp';
 
// TESTS
import '@/registerServiceWorker'
// COMPONENTS
import App from '@/App.vue'
// ROUTER
import router from '@/Router'
// STYLES
import '@/Styles/Global.scss'
// CONFIG
import '@/Config/FaIcons'
import '@/Config/Fragment'
import '@/Config/Firebase'

// Vue.use(WebpPlugun);
Vue.use(VueWebP)

Vue.prototype.$loaded = false

Vue.prototype.$importAll = (r) => r.keys().map(r)

let vh = window.innerHeight * 0.01;
document.documentElement.style.setProperty('--vh', `${vh}px`);
window.addEventListener('resize', () => {
	let vh = window.innerHeight * 0.01;
	document.documentElement.style.setProperty('--vh', `${vh}px`);
});

Vue.config.productionTip = false


Vue.directive('bg', function (el, binding) {
	el.style.backgroundImage = `url(${binding.value})`
})
new Vue({
	router,
	render: h => h(App)
}).$mount('#app')
